import { DataItem } from '../../store';
import { TR } from '../tr';
import { TH } from '../th';

const Card = (props: DataItem) => {
  const { subjectName, groupName, course, studentsNumber, 
          lecturesHours, semestr, laboratoryHours, practicHours,
          seminarHours, offset, exam, podgroups, uniqueId } = props;
  return (
    <div>
      <div className="table-wrap">
        <table className='table-first'>
          <thead>
            <tr>
              <th colSpan={4}>{subjectName}</th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td></td>
              <td>{groupName}</td>
              <td></td>
              <td>{course}</td>
            </tr>
            <tr>
              <td> </td>
              <td>{studentsNumber}</td>
              <td></td>
              <td>{semestr}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div className="table-wrap">
        <table>
          <thead>
            <TH podgroups={podgroups} uniqueId={uniqueId} />
          </thead>
          <tbody>
            <TR uniq='lecturesHours' teacher={'lectureTeacher'} podgroups={podgroups} uniqueId={uniqueId} title={''} data={lecturesHours} isDisabled={lecturesHours === '0'} />
            <TR uniq='laboratoryHours' teacher={'laboratoryTeacher'} podgroups={podgroups} uniqueId={uniqueId} title={' '} data={laboratoryHours} isDisabled={laboratoryHours === '0'} />
            <TR uniq='practicHours' teacher={'practiceTeacher'} podgroups={podgroups} uniqueId={uniqueId} title={''} data={practicHours} isDisabled={practicHours === '0'} />
            <TR uniq='seminarHours' teacher={'seminarTeacher'} podgroups={podgroups} uniqueId={uniqueId} title={''} data={seminarHours} isDisabled={seminarHours === '0'} />
            {
              exam === true && offset === false ? <TR teacher={'examTeacher'} podgroups={podgroups} uniq='exam' uniqueId={uniqueId} title={''} data={''} isDisabled={false} /> : 
              exam === false && offset === true ? <TR teacher={'offsetTeacher'} podgroups={podgroups} uniq='offset' uniqueId={uniqueId} title={''} data={''} isDisabled={false} /> :
              exam === false && offset === false ? null : null
            }
            {podgroups.length > 1 ? <TR teacher={'no'} uniq={'people'} title={' '} podgroups={podgroups} uniqueId={uniqueId} /> : null }
            <TR teacher={'no'} uniq={'prim'} uniqueId={uniqueId} title={' (  )'} data={''} field={<textarea />} numTD={podgroups.length} />
          </tbody>
        </table>
      </div>
    </div>
  )
};

export { Card }